.color-selector > li {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0 8px 8px 0;
  background-color: #3f51b5;
  border-radius: 100%;
}

.color-selector > li:hover {
  opacity: .8;
}

.color-selector > li:before {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: inherit;
  height: inherit;
  content: "";
  background: inherit;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: inherit;
}

.color-selector > li input[type="radio"] {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: inherit;
  height: inherit;
  cursor: pointer;
  border-radius: inherit;
  opacity: 0;
}

.color-selector > li input[type="radio"]:disabled {
  cursor: not-allowed;
}

.color-selector > li label {
  position: relative;
  font-family: "Material-Design-Iconic-Font";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
}

.color-selector > li input[type="radio"]:checked + label:after {
  position: absolute;
  top: 0;
  left: 8px;
  display: inline-block;
  margin-top: -2px;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  content: "";
}

.color-selector > li.color-selector-disabled {
  background-color: #bdbdbd !important;
}

.color-selector > li.color-selector-disabled input[type="radio"]:disabled {
  cursor: not-allowed;
}

