$roboto-font-path: "/fonts";
@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  font-style: normal;
  src: url('#{$roboto-font-path}/Roboto/Roboto-300.eot');
  src: url('#{$roboto-font-path}/Roboto/Roboto-300.eot?#iefix') format('embedded-opentype'),
       local('Roboto Light'),
       local('Roboto-300'),
       url('#{$roboto-font-path}/Roboto/Roboto-300.woff2') format('woff2'),
       url('#{$roboto-font-path}/Roboto/Roboto-300.woff') format('woff'),
       url('#{$roboto-font-path}/Roboto/Roboto-300.ttf') format('truetype'),
       url('#{$roboto-font-path}/Roboto/Roboto-300.svg#Roboto') format('svg');
}

