/** _login.scss */
.page-login-v2 {
  height: 100%;

  &:before{
    background-image: url("/images/login.jpg");
  }

  &.page-dark.layout-full {
    &:after {
      background-color: rgba($gray-base, (60/100));
    }
  }

  .page-brand-info {
    margin: 220px 100px 0 90px;

    .brand-img {
      vertical-align: middle;
    }
    .brand-text {
      display: inline-block;
      margin: 11px 0 11px 20px;
      vertical-align: middle;
    }
    p {
      max-width: 650px;
      opacity: .6;
    }
  }

  .has-error {
    span.help-block {
      color: $font-error;
    }
  }

  .page-login-main {
    position: absolute;
    top: 0;
    right: 0;
    height: auto;
    min-height: 100%;
    padding: 150px 60px 180px;
    color: $login-main-font-color;
    background: $login-main-background-color;

    .brand {
      margin-bottom: 60px;
    }
    .brand-img {
      vertical-align: middle;
    }
    .brand-text {
      display: inline-block;
      margin: 11px 0 11px 20px;
      color: $login-brand-font-color;
      vertical-align: middle;
    }
  }

  form {
    width: 350px;
    margin: 45px 0 20px;

    > button {
      margin-top: 38px;
    }
    a {
      margin-left: 20px;
    }
  }

  footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 50px 60px;
    text-align: center;
  }
}

.page-login-v2,.page-dark,.layout-full {
  &:after {
    background-color: rgba(33, 33, 33, .6);
  }
}

.page-login-v2 .social .icon,
.page-login-v2 .social .icon:hover,
.page-login-v2 .social .icon:active {
  color: $login-social-icon-active-color;
}
@media (min-width: 992px) {
  .page-login-v2 {
    .page-content {
      padding-right: 500px;
    }
  }
}
@media (max-width: 768px) {
  .page-login-v2 {
    .page-login-main {
      padding-top: 60px;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .page-login-v2 {
    .page-login-main {
      padding-top: 80px;
    }
    .page-brand-info {
      margin: 160px 0 0 35px;

      > p {
        color: transparent;
        opacity: 0;
      }
    }
  }
}
@media (max-width: 767px) {
  .page-login-v2 {
    .page-login-main {
      width: 100%;
      padding-top: 60px;
    }
    form {
      width: auto;
    }
  }
}
@media (max-width: 480px) {
  .page-login-v2 {
    .page-brand-info {
      margin: 220px 0 0;
    }
    .page-login-main {
      padding: 50px 30px 180px;
    }
    form {
      width: auto;
    }
    footer {
      margin: 50px 30px;
    }
  }
}
