
@mixin validation-state-focus($color) {
  .select2-dropdown,
  .select2-selection {
    border-color: $color;
  }

  .select2-container--open .select2-selection {
    border-color: $color;
  }

  &.select2-drop-active {
    border-color: $color;

    &.select2-drop.select2-drop-above {
      border-top-color: $color;
    }
  }
}

@mixin dropdown-arrow() {
  .select2-selection--single {
    .select2-selection__arrow b {
      border-color: transparent transparent $dropdown-arrow-color transparent;
      border-width: 0 $caret-width-lg $caret-width-lg $caret-width-lg;
    }
  }
}

@mixin form-material-control-underline($text-color, $border-color) {
  background-image: linear-gradient($text-color, $text-color), linear-gradient($border-color, $border-color);
}
