// base in remark
.animsition,
.animsition-overlay {
  position:relative;
  opacity:0;
  animation-fill-mode: both;
}

// overlay option
.animsition-overlay-slide {
  position:fixed;
  height:100%;
  width:100%;
  z-index:1;
  background-color: #ddd;
}

// end base in remark

